<template>
    <div v-if="ready">
        <form @submit.prevent="handleSubmit">
            <div class="card col-12" style='height:auto;margin-bottom:1.5rem;margin-top:0'>
                <div class="card-header">
                    <strong>Batch Push Notification</strong>
                </div>            
                <div class="card-body card-block">                                      
                    <div class="col-12 px0">
                        <label style="margin-bottom:1rem"><u>Please Enter Message</u></label>
                        <textarea class="form-control" rows="6" v-model="message" required placeholder="Enter Message"></textarea>               
                    </div>                 
                </div>
                <div class="card-footer">
                    <div class="flex">
                        <button type="submit" class="btn btn-primary btn-sm">
                            <i class="fa fa-paper-plane"></i> Submit
                        </button>                   
                        <button type="button" class="btn btn-danger btn-sm" @click="return_prev">
                            <i class="fa fa-undo"></i> Return
                        </button>
                    </div>
                </div>
            </div>  
        </form>      
    </div>
</template>
<script>
export default {
    data(){
        return{
            ready:false,
            message:'',
        }
    },
    created(){
        this.ready=true;
    },
    mounted(){

    },
    methods:{
        handleSubmit(){
            this.$confirm('Confirm Sending the following message : '+this.message+' ?')
                .then(
                    ()=>{
                        var formData = new FormData();
                        formData.append('message',this.message);
                        axios.post('inventory/web/backend/batch_fcm.php',formData)
                            .then(
                                (response)=>{
                                    if(response.data=='OK'){
                                        this.$alert("Successfully Sent Notification");
                                        this.message = '';
                                    }
                                }
                            )
                    }
                )
        },
        return_prev(){
            this.$router.go(-1);
        }
    }
}
</script>
<style scoped>
    .px0{
        padding:auto 0 auto 0;
    }
</style>


